@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100&display=swap);
video {
    border-radius: 10px;
    width: inherit;
}

.vidsContainer {
    display: flex;
    flex-direction: column;
}

.myVideo, .userVideo {
    display: grid;
    padding: 10px;
    margin: 2px;
    border-radius: 10px;
}

.myVideo {
    width: 90%;
}

.userVideo {
    width: 99%;
}
.optionsContainer {
    margin: 2px;
    padding: 10px;
}
.compiler {
    display: flex;
    flex-direction: row;
    margin: 2px;
    padding: 10px;

}

.fields {
    width: 100%;
}

textarea {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: larger;
    resize: vertical;
    overflow: auto;
}

#source {
    height: 500px;
}

#input {
    height: 50px;
}

#output {
    height: 100px;
}
.main {
    display: flex;
}

.left {
    flex: 30% 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.right {
    flex: 70% 1;
}
  

@media (max-width: 800px) {
    .main {
        flex-direction: column;
    }
    .right, .left {
        flex: 100% 1;
    }
}
body {
    margin: 0;
    padding: 0;
    background: #171717;
    color: #eeeeee;
    font-size: large;
    font-family: 'Outfit', sans-serif;
}

button {
    background-color: #00adb5;
    color: white;
    border-color: #00adb5;
    border-radius: 0px 10px 10px 0px;
    width: 50px;
    height: 50px;
}

img {
    height: 15px;
}

button:hover {
    background-color: #00acb5d3;
}

input {
    background-color: #000;
    color: #eeeeee;
    height: 50px;
    width: 75%;
    border-radius: 10px 0px 0px 10px;
    border-color: #000;
    margin-top: 2px;
}

textarea {
    background-color: #000;
    border-radius: 10px;
    color: #fff;
}

select {
    background-color: #000;
    color: #eee;
    height: 50px;
    width: 200px;
    font-size: larger;
    padding-left: 10px;
    border-radius: 10px 0px 0px 10px;
    border-color: #00adb5;
}
